/* ==========================================================================
   #BUTTONS
   ========================================================================== */

/**
 * Estructura y algunos resets para los botones. Aquí no tienen que ir estilos
 * visuales, solo estructurales
 */

@mixin btn-base($padding: null) {
  display: inline-flex;
  margin: 0;

  @if $padding {
    padding: $padding;
  }

  border: 0;
  background: none;
  font-size: inherit;
  cursor: pointer;

  justify-content: center;
  align-items: center;
  appearance: none;
}
